import React, { Component } from 'react';
import logo from './logo.svg';
import Navigating from './Components/Navigating'
import './App.css';
import Logo from './Components/Logo'
import ImgeForm from './Components/ImageForm';
import Rank from './Components/Rank';
import Particles from 'react-particles-js';
import Clarifai from 'clarifai';
import FaceDetection from './Components/FaceDetection';
import SignIn from './Components/SignIn'

const app = new Clarifai.App({apiKey: 'b22b82bb9d7545408da0f22f52605f31'});

const particlesOptions ={
  particles:{
    number:{
      value: 50,
      density:{
        enable: true,
        value_area:300
      }
    }

  }
}
class App extends Component{
  constructor(){
    super();
    this.state={
      input:' ',
      ImageUrl:'',
      box:[],
      rout:'signIn'
    }
    
  }
  
  detectFace=(data)=>{
    const details=[]
    const arr=data.outputs[0].data.regions
    const detect=data.outputs[0].data.regions[0].region_info.bounding_box
    const imgUrl=document.getElementById('inputImg')
      const IMgwidth=Number(imgUrl.width) 
      const IMgheight=Number(imgUrl.height)
      // console.log(Number(imgUrl.height))
      // console.log(Number(imgUrl.width))
    arr.forEach(element => {
      const detect=element.region_info.bounding_box
      const onebyDetails={
        leftCol:detect.left_col * IMgwidth ,
        rightCol:IMgwidth-(detect.right_col * IMgwidth),
        topCol:detect.top_row * IMgheight,
        bottomCol:IMgheight-(detect.bottom_row * IMgheight)
      }
      details.push(onebyDetails)
     
    });

    return details;
  
  }

  getFaces=(box)=>{
    //console.log(box)
    this.setState({box:box})
  }

  onInputChange=(event)=>{
    this.setState({input:event.target.value})
  }
  onSubmit=(event)=>{
    // console.log('click');
    this.setState({ImageUrl:this.state.input})
    app.models.predict(Clarifai.FACE_DETECT_MODEL, this.state.ImageUrl)
        .then(response => {
          this.getFaces(this.detectFace(response));
        })
        .catch(err => {
          console.log(err);
        });
  }

  onChange=()=>{
    this.setState({rout:'home'})
  }
  render(){
    return (
      <div className="App">
        <Particles className='particle' params={particlesOptions} />
          <Navigating />
          {this.state.rout === 'signIn' ? <SignIn  onChange={this.onChange}/>
          : <div>
            <Logo />
          <Rank />
          <ImgeForm onInputChange={this.onInputChange} onSubmit={this.onSubmit}/>
          <FaceDetection ImageUrl={this.state.ImageUrl} box={this.state.box} />    
          </div>
          
          }
          
      </div>
    );
  }
}

export default App;
