import React from 'react';
import './logo.css';

const ImgeForm=({onInputChange,onSubmit})=>{
    return(
        <div>
            <p className='f3'>This App will detect your face</p>
            <div className='align  pa0'>
                <div className='f3 align br2 shadow-2  pa2'>
                        <input type='text' className='f4 pa2 align w-50' onChange={onInputChange}></input>
                        <button className='bg-purple white pa0 pv2 align border grow w-20 f5 link' onClick={onSubmit}>DETECT</button>
                </div>
            </div>
        </div>
        
    )
}

export default ImgeForm