import React, { Component } from 'react';
import './logo.css'
// import openBox from './OpenBox'
import './face.css'

class FaceDetection extends Component{
    constructor(props){
        super()
    }

    getItem=()=>{
        return(
            this.props.box
        )
    }

    render(){
    console.log(this.props.box)

        return(
            <div className='align ma'>
                <div className='absolute mt2'>
                    <img alt='' id='inputImg' src={this.props.ImageUrl} width='300px' height='auto' />
                    {
                        this.getItem().map((element,i)=>{
                            return(
                                <div className='bounding_box' style={{top:element.topCol,bottom:element.bottomCol,left:element.leftCol,right:element.rightCol}}></div>
                            )
                            
                        })
                    }
                </div>
                
            </div>
        )
    }
    }
   
    
    


export default FaceDetection